<template>
    <div class="board-detail">
        글번호 : {{ detail.bno }}<br>
        글제목 : {{ detail.btitle }}
        <button v-on:click="update">수정</button>
        <button v-on:click="deletepost">삭제</button><br>
        글쓴이 : {{ detail.m_name }}<br>
        쓴날짜 : {{ detail.bdate }}<br>
        좋아요 : {{ detail.blike }}<br>
        내 용 : {{ detail.bcontent }}<br>
        <div v-for="(c, index) in comment" :key="index">
            글번호: {{ c.c_no}} 작성자: {{ c.m_name}} <button v-on:click="updatecomment(c.c_no, detail.bno)">수정</button><button v-on:click="deletecomment(c.c_no)">삭제</button><br>댓글작성일: {{ c.c_date}}<br>
            댓글내용: {{ c.c_comment}}<br>
        </div>

        <button v-on:click="writecomment">댓글쓰기</button>
        <button v-on:click="board">게시판으로</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            requestBody : this.$route.query,
            detail: {
                bno: this.$route.query.bno,
                btitle: '',
                bcontent: '',
                bdate: '',
                m_name: '',
                blike: '',
            },
            comment: [],
        }
    },
    mounted() {
        this.boardDetail();
    },
    methods: {
        board() {
            this.$router.push('/boardList');
        },

        writecomment() {
            this.$router.push('/writecomment?bno=' + this.$route.query.bno);
        },

        boardDetail() {
            this.$axios.get(this.$server+'/detail?bno=' + this.$route.query.bno)
                .then((res) => {
                    this.detail = res.data.detail;
                    this.comment = res.data.comment;
                })
                .catch((err) => {
                    alert('오류가 발생했습니다.' + err);

                });
        },
        deletepost() {
            if (confirm('삭제하시겠습니까?')) {
                //alert(this.detail.bno + "!");
                alert('삭제합니다.');
                this.$axios({
                  url:this.$server+'/delete',
                  method: 'post',
                  params:{bno:this.detail.bno}
                })
                .then((res) => {
                    if(res.data.result == 1){
                        alert('데이터베이스에서 삭제했습니다.');
                        this.$router.push({
                            path: '/boardList',
                            params: this.requestBody
                        });
                    } else{
                        alert('oops! 문제가 발생했습니다. 다시 시도하지 마세요.');
                    }
                    //alert('통신 결과 : ' + res.data.result);
                   
                })
                .catch((err) =>{
                    alert('에러가 발생했습니다.' + err);
                });
            }
        },

        deletecomment(cno) {
            if (confirm('삭제하시겠습니까?')) {
                //alert(cno + "!");
                alert('삭제합니다.');
                this.$axios({
                  url:this.$server+'/deletecomment?cno=' + cno,
                  method: 'post',
                  params:{cno:cno}
                })
                .then((res) => {
                    if(res.data.result == 1){
                        alert('데이터베이스에서 삭제했습니다.');
                        this.$router.push('/detail?bno='+ this.$route.query.bno);
                        this.$router.go(0);
                    } else{
                        alert('oops! 문제가 발생했습니다. 다시 시도하지 마세요.');
                    }
                    //alert('통신 결과 : ' + res.data.result);
                   
                })
                .catch((err) =>{
                    alert('에러가 발생했습니다.' + err);
                });
            }
        },

        update(){
            if(confirm('수정하시겠습니까?')){
                this.$router.push({
                    path:'/update',
                    //bno:this.detail.bno
                    query:this.requestBody
                });
            }
        },

        updatecomment(cno, bno){
            if(confirm('수정하시겠습니까?')){
                //alert(cno + "!");
                this.$router.push({
                    path:'/updatecomment',
                    query: {cno:cno, bno:bno},
                });
            }
        }

    }
}  
</script>
<style scoped></style>