<template>
    <div class="writecomment">
        <h1>댓글쓰기</h1>    
        <input v-model="content">
        <button @click="writecomment">댓글쓰기</button>
    </div>
</template>

<script>

export default{
    data(){
        return{
            requestBody: this.$route.query,
            bno: this.$route.query.bno,
            content:null,
        }
    },
    methods:{
        writecomment(){
            let saveData = {};
            saveData.content = this.content;
            saveData.bno = this.requestBody.bno;
            //alert(saveData.title);
            this.$axios.post(this.$server+'/writecomment', JSON.stringify(saveData), {
                headers: {"Content-Type" : "application/json"}
            })
            .then((res) => {
                if(res.data.result == 1) {
                    alert("데이터베이스에 글을 저장했습니다.");
                    //location.href="/boardList";
                    this.$router.push("/boardList");
                } else{
                    alert("ooops! 문제가 발생했습니다.");
                }
            })
            .catch((err) => {
                alert("문제가 발생했습니다." + err);
            });
        }
    }
}

</script>

<style>
.writecomment{
    margin: 0 auto;
    padding: 5px;
    width: 800px;
    height: 500px;
    background-color: bisque;
}

.writecomment input{
    width: 100%;
    height: 30px;
}

</style>