<template>
    <div class="write">
        <h1>댓글 수정하기</h1>
        <input v-model="content">
        <button @click="updatecomment">수정하기</button>
    </div>
</template>

<script>

export default {
    data() {
        return {
            requestBody: this.$route.query,
            content: null,
            detail: {
                bno: this.$route.query.bno,
                cno: this.$route.query.cno,
                content: '',
            },
            comment: {
                m_no: '',
                bno:'',
                c_no:'',
                c_date:'',
                m_name:'',
                c_comment:''
            },
        };
    },
    mounted() {
        this.commentDetail();
    },
    methods: {
        commentDetail() {
            //alert(this.detail.cno + "!");
            this.$axios.get(this.$server + '/commentdetail?cno=' + this.$route.query.cno + '&bno=' + this.$route.query.bno)
                .then((res) => {
                    this.comment = res.data.comment;
                    this.content = this.comment.c_comment;
                })
                .catch((err) => {
                    alert('오류가 발생했습니다.' + err);

                });
        },
        updatecomment() {
            let saveData = {};
            saveData.content = this.content;
            saveData.cno = this.$route.query.cno;
            //alert(saveData.title);
            this.$axios.patch(this.$server + '/updatecomment', JSON.stringify(saveData), {
                headers: { "Content-Type": "application/json" }
            })
                .then((res) => {
                    if (res.data.result == 1) {
                        alert("데이터베이스에 글을 저장했습니다.");
                        //location.href="/boardList";
                        this.$router.push("/detail?bno="+this.$route.query.bno);
                    } else {
                        alert("ooops! 문제가 발생했습니다.");
                    }
                })
                .catch((err) => {
                    alert("문제가 발생했습니다." + err);
                });
        }
    }
}

</script>

<style>
.updatecomment{
    margin: 0 auto;
    padding: 5px;
    width: 800px;
    height: 500px;
    background-color: bisque;
}

.updatecomment input{
    width: 100%;
    height: 30px;
}
</style>